.list {
  list-style: none;
  margin-left: -0.5em;
}

.list__item {
  padding: 0.5em;
  line-height: 1.5em;
}

.list__item:nth-of-type(odd) {
  background: -webkit-gradient(linear, left top, right top, from(rgba(0, 0, 0, 0.05)), to(rgba(0, 0, 0, 0)));
  background: -webkit-linear-gradient(left, rgba(0, 0, 0, 0.05), rgba(0, 0, 0, 0));
  background: -o-linear-gradient(left, rgba(0, 0, 0, 0.05), rgba(0, 0, 0, 0));
  background: linear-gradient(90deg, rgba(0, 0, 0, 0.05), rgba(0, 0, 0, 0));
}



